<script>
  import { numberWithCommas } from "./../globals.js";
  import { goto } from "@sveltech/routify";
  import axios from "axios";
  import { onMount } from "svelte";
  export let countryCode;

  let countryData = [];
  onMount(() => {
    axios
      .get(`https://corona.lmao.ninja/v2/countries/${countryCode}`)
      .then(res => {
        countryData = res.data;
      });
  });
</script>

<style lang="scss">
  .container {
    flex-shrink: 1;
    border: 3px solid lightsalmon;
    border-radius: 5px;
    margin: 5rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 1rem 2rem;
    height: 23rem;
    cursor: pointer;
    width: 17rem;

    .name {
      font-weight: bold;
      margin-bottom: 1rem;
    }

    .detail {
      margin: 1rem;
      padding: 1rem;
      font-weight: bold;
      font-size: 1.5rem;
    }
  }

  img {
    height: 4rem;
    width: 6rem;
    border: 1px solid lightgrey;
  }
</style>

<div class="container" on:click={() => $goto('/country/' + countryCode)}>
  {#if countryData.countryInfo}
    <div class="name">{countryData.country}</div>
    <img alt={countryCode} src={countryData.countryInfo.flag} />
    <div class="detail">
      <span style="color:#b3b300">{numberWithCommas(countryData.cases)}</span>
      total
    </div>
    <div class="detail">

      <span style="color: red">{numberWithCommas(countryData.deaths)}</span>
      deaths
    </div>
    <div class="detail">

      <span style="color:#429028">
        {numberWithCommas(countryData.recovered)}
      </span>
      recovered
    </div>
  {/if}
</div>
